.overlay{
	position:fixed;
	top:0;
	width:100%;
	height:100%;
	background-color:rgba(0,0,0,0.5);
	z-index:4;
}

.showCartPopup{
	position:absolute;
	top:50%;
	left:50%;
	transform:translate(-50%,-50%);
	width:60%;
	max-width:850px;
	background-color:#fff;
	border:10px solid black;
	text-align:center;
}

.showCartPopup h1{
	width:100%;
	min-height:80px;
	margin-top:0;
	background-color:#5CB0B9;
	text-align:center;
	padding-top:20px;

}

.cardInfoContainer{
	display:flex;
	justify-content:flex-start;
	width:100%;
	height:70%;
	min-height:400px;
	/*border:2px solid black;*/
	padding:20px;
}

.noItemInCart h1{
	position:absolute;
	background-color:white;
	width:90%;
	top:50%;
	left:50%;
	transform:translate(-50%,-50%);
}

.itemInCartContainer{
	display:flex;

}

.itemInCart{
	width:45%;
	height:90%;
	border:2px solid #000;
}

.itemInCart img{
	width:100%;
	height:100%;
}

.itemInCartInfo{
	margin-left:10px;
}

.itemInCartInfo h2{
	margin-bottom:20px;
}

.priceTable{
	width:90%;
	height:50%;
	margin-left:20px;
}

.priceTable td{
	font-size:2em;
	font-weight:600;
	text-align:left;

}

.popupOkBtn{
	width:200px;
	min-height:80px;
	background-color:#0B7B9A;
	border:1px solid black;
	color:#fff;
	font-size:2.3em;
	margin:20px;
}